import React, { Suspense, useRef } from "react"
import { Canvas, useFrame, useThree } from "@react-three/fiber"
import { OrbitControls, softShadows } from "@react-three/drei"
import { Physics, useBox, usePlane, useSphere, Debug } from "@react-three/cannon"
import * as THREE from "three"
import Roboto from "../../assets/Roboto_Regular.json"

import Car from "../Car/Car"

softShadows()

function Text({ args, ...props }) {
  const font = new THREE.FontLoader().parse(Roboto)
  const [box] = useBox(() => ({ args: [0.5, 0.5, 0.5, 1], mass: 1, ...props }))

  const textOptions = {
    font,
    size: 1,
    bevelEnabled: true,
    bevelThickness: 0.3,
    bevelSize: 0.3,
    bevelOffset: 0,
    bevelSegments: 3,
    height: 0.3
  }

  return (
    <mesh castShadow receiveShadow ref={box}>
      <textGeometry translate={[-1, 0, -0.5]} attach="geometry" args={[args, textOptions]} />
      <meshNormalMaterial attach="material" />
    </mesh>
  )
}

function Floor(props) {
  const [ref] = usePlane(() => ({ rotation: [-Math.PI / 2, 0, 0], ...props }))
  return (
    <mesh ref={ref} receiveShadow>
      <planeBufferGeometry attach="geometry" args={[100, 100]} />
      <shadowMaterial attach="material" transparent opacity={0.4} />
    </mesh>
  )
}

function Home() {
  return (
    <Canvas className="canvas" shadows colorManagment gl={{ alpha: false }} camera={{ position: [-3, 3, 16], fov: 50 }}>
      <color attach="background" args={["lightblue"]} />
      <fog attach="fog" args={["white", 0, 40]} />
      <hemisphereLight intensity={0.35} />
      <directionalLight position={[10, 10, 10]} angle={0.3} penumbra={1} intensity={2} castShadow shadowBias={-0.001} shadow-mapSize-width={1024} shadow-mapSize-height={1024} shadow-camera-far={50} shadow-camera-left={-10} shadow-camera-right={10} shadow-camera-top={10} shadow-camera-bottom={-10} />
      <Physics broadphase="SAP">
        <Suspense fallback="Loading...">
          <Car position={[-4, 2, 3]} rotation={[0, 0, 0]} angularVelocity={[0, 0.5, 0]} wheelRadius={0.6} />
          <Text args="T" position={[-6, 0, -3]} />
          <Text args="H" position={[-4, 0, -2]} />
          <Text args="A" position={[-2, 0, -1]} />
          <Text args="N" position={[0, 0, 0]} />
          <Text args="K" position={[2, 0, 1]} />
          <Text args="S" position={[4, 0, 2]} />

          <Floor />
        </Suspense>
      </Physics>
      <OrbitControls />
    </Canvas>
  )
}

export default Home
